@charset "UTF-8";
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select {
  margin: 0;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img,
video {
  height: auto;
  max-width: 100%;
}

img {
  display: block;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

:root {
  --background: #24273a;
  --pale-violet: #c6a0f6;
  --background2: #1e2030;
  --grey: #6e738d;
  --background-light: #363a4f;
  --border-grey: #494d64;
  --backlight: rgba(237, 135, 150, 0.05);
  --red: #ed8796;
  --pink: #ee99a0;
}

@font-face {
  font-family: "Open Sans";
  src: local("../../assets/font/OpenSans-VariableFont_wdth,wght.ttf");
  font-family: "Open Sans italic";
  src: local("../../assets/font/OpenSans-Italic.ttf");
}
html {
  font-size: 16px;
}

body {
  font-family: "Open Sans";
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--background);
  width: 100%;
  height: 100%;
  margin-top: 4.3775rem;
  color: #fff;
}

.body_noscroll {
  overflow-y: hidden;
}

#root {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

hr {
  width: 100%;
  color: var(--grey);
  height: 1px;
  margin: 0.4rem 0;
  opacity: 0.5;
}

.section-heading {
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.login-button {
  display: inline-block;
  width: 100%;
  background-color: var(--pale-violet);
  border: none;
  cursor: pointer;
  color: var(--background);
  border-radius: 0.25rem;
  transition: 0.1s ease;
  padding: 0.5rem;
  font-weight: 600;
  text-align: center;
  font-size: 0.875rem;
}
.login-button:hover {
  background-color: var(--background2);
  color: #fff;
  transition: 0.1s ease;
}

.logout-button {
  width: 100%;
  background-color: var(--red);
  border: none;
  cursor: pointer;
  color: var(--background2);
  border-radius: 0.25rem;
  transition: 0.1s ease;
  padding: 0.5rem;
  font-weight: 600;
  text-align: center;
  font-size: 0.875rem;
}
.logout-button:hover {
  background-color: var(--background2);
  color: #fff;
  transition: 0.1s ease;
}

.header {
  position: fixed;
  width: 100%;
  height: 4.3775rem;
  top: 0;
  left: 0;
  padding: 0 1.25rem;
  background-color: var(--background2);
  z-index: 10;
}
.header-container {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.header-container__logo {
  position: absolute;
  display: block;
  left: 50%;
  transform: translateX(-50%);
  align-self: center;
  height: 90%;
}
.header-container__logo img {
  height: 100%;
}
.header-container__profile {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  width: max-content;
}
.header-container__profile img {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}
.header-container__logout-button:hover {
  background-color: var(--pale-violet);
}
.header-nav {
  display: none;
}
.header-nav ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
}
.header-nav ul li {
  font-weight: 600;
  font-size: 0.8rem;
  transition: 0.05s ease-out;
}
.header-nav ul .container {
  padding: 0.3rem;
  position: relative;
  border-radius: 0.25rem 0.25rem 0 0;
}
.header-nav ul .container:hover {
  background-color: var(--grey);
  border-bottom: none;
  color: var(--background);
}
.header-nav ul .container:hover .header-profile__dropdown {
  display: flex;
}
.header-nav nav > ul li:not(.container) {
  margin-top: 2px;
}
.header-nav nav > ul li:not(.container) a {
  display: inline-block;
}
.header-nav nav > ul li:not(.container) a::after {
  display: block;
  content: "";
  border-bottom: 2px solid var(--pale-violet);
  transform: scaleX(0);
  transition: transform 0.15s ease-in-out;
}
.header-nav nav > ul li:not(.container) a:hover::after {
  transform: scaleX(1);
}
.header-nav__login-button:hover {
  background-color: var(--red);
}
.header-profile {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.header-profile__logout-button:hover {
  background-color: var(--pale-violet);
}
.header-profile__dropdown {
  display: none;
  flex-direction: column;
  position: absolute;
  width: 100%;
  height: auto;
  top: 100%;
  left: 0;
  background-color: var(--grey);
  border-radius: 0 0 0.25rem 0.25rem;
  padding-bottom: 0.5rem;
  width: 100%;
  line-height: 1.25rem;
  text-align: center;
  color: var(--background);
  transition: background-color 0.1s;
}
.header-profile__dropdown a:hover {
  background-color: #fff;
  transition: background-color 0.1s;
}

.bottom-nav {
  position: fixed;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  height: 3.375rem;
  bottom: 0;
  left: 0;
  padding: 0 4.19rem;
  gap: 1rem;
  background-color: var(--background2);
  transform: translateY(0);
  transition: transform 0.3s ease;
}
.bottom-nav__button {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}
.bottom-nav__button .container::after {
  display: block;
  content: "";
  border-bottom: 1px solid var(--pale-violet);
  transform: scaleX(0);
  transition: transform 0.2s ease-out;
}
.bottom-nav__button .container img {
  filter: invert(99%) sepia(88%) saturate(2%) hue-rotate(92deg) brightness(113%) contrast(100%);
  opacity: 0.5;
  padding-bottom: 0.2rem;
}
.bottom-nav__button_active .container > img {
  filter: invert(77%) sepia(58%) saturate(3045%) hue-rotate(207deg) brightness(103%) contrast(93%);
  opacity: 1;
}
.bottom-nav__button_active .container::after {
  transform: scaleX(1);
}
.bottom-nav_hidden {
  transform: translateY(100%);
}

.footer {
  width: 100%;
  background-color: var(--background-light);
}
.footer .footer-list {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  padding: 0.25rem 0;
}
.footer .footer-list__element {
  font-weight: 700;
  margin-right: 0.5rem;
  font-size: 0.8rem;
  line-height: 1rem;
}
.footer .footer-list__element a:hover {
  color: var(--pale-violet);
}
.footer .footer-grid {
  display: flex;
  align-items: center;
  margin-bottom: 0.75rem;
}
.footer p {
  font-size: 0.75rem;
}
.footer p a {
  text-decoration: underline;
}
.footer p a:hover {
  color: var(--pale-violet);
}
.footer .container {
  max-width: 950px;
  margin-right: auto;
  margin-left: auto;
  padding: 1.5rem 1.25rem 1.5rem 1.25rem;
}

.film-link {
  position: relative;
  border-radius: 0.25rem;
  border: 1px solid var(--border-grey);
  width: fit-content;
  overflow: hidden;
}
.film-link:hover {
  border-color: #fff;
}

.home {
  position: relative;
  display: flex;
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: column;
  padding: 0 1.25rem;
}
.home__heading {
  font-family: inherit;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.25rem;
  margin-top: 16.5rem;
  text-align: center;
}
.home__heading > span {
  color: var(--pale-violet);
}
.home__heading_authorized {
  margin-top: 1.31rem;
  text-align: left;
}
.home__greeting {
  font-size: 0.875rem;
  min-width: 11rem;
  margin-bottom: 1.31rem;
}
.home__greeting_unauthorized {
  margin-top: 1.2rem;
  text-align: center;
  margin-bottom: 2rem;
}
.home__banner {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-height: 150px;
  background-color: #fff;
  margin: 2.6rem 0 3rem 0;
  overflow: hidden;
}
.home__banner img {
  object-fit: fill;
}
.home-unauthorized {
  position: absolute;
  top: 0;
  left: 0;
  max-height: 250px;
  height: 250px;
  width: 100%;
  background-image: url("/public/assets/home/home-background.jpg");
  background-position: center;
  background-size: cover;
}
.home__login-button {
  width: 50%;
  padding: 0.7rem;
}
.home-features {
  margin: 2.6rem 0 3rem 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.home-features .home-feature {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 2rem 1rem;
  gap: 1rem;
  color: #fff;
  border-radius: 0.25rem;
  background-color: var(--background2);
  transition: 0.1s ease;
}
.home-features .home-feature__icon {
  filter: invert(100%) sepia(0%) saturate(7491%) hue-rotate(196deg) brightness(99%) contrast(107%);
  width: 25px;
  max-width: 25px;
}
.home-features .home-feature__text {
  font-weight: 600;
}
.home-features .home-feature:hover {
  background-color: var(--red);
  color: var(--background2);
  transition: 0.1s ease;
}
.home-features .home-feature:hover .home-feature__icon {
  filter: invert(8%) sepia(7%) saturate(4062%) hue-rotate(196deg) brightness(91%) contrast(88%);
}

.popular-month__heading, .latest-news__heading, .recent-articles__heading, .popular-month-lists__heading {
  font-family: inherit;
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}
.popular-month-section {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  gap: 0.3rem;
}
.popular-month-section__film-link {
  width: 32%;
}
.popular-month-lists-grid {
  display: flex;
  flex-direction: column;
  gap: 0.9rem;
}
.popular-month-lists-grid__element {
  width: 100%;
}
.popular-month-lists-grid__element h3 {
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.25rem;
}
.popular-month-lists-grid__element .container {
  display: flex;
  width: fit-content;
  justify-content: flex-start;
  flex-wrap: nowrap;
  height: auto;
  border-radius: 0.25rem;
  border: 1px solid var(--grey);
  overflow: hidden;
  margin-bottom: 0.25rem;
}
.popular-month-lists-grid__element .container:hover {
  border-color: #fff;
}
.popular-month-lists-grid__element .container__film-link {
  width: 16.666%;
}
.popular-month-lists-grid__element .container__film-link:hover > .lists-buttons {
  display: none;
}
.recent-articles .container {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
}

.article-card {
  min-height: 15rem;
  margin-right: 0.75rem;
  min-width: 15rem;
  background-color: var(--background2);
  border-radius: 0.25rem;
}
.article-card > div {
  padding: 0.75rem;
}
.article-card > div a:hover {
  color: var(--pale-violet);
}
.article-card__heading {
  font-weight: 700;
  margin-bottom: 0.5rem;
  font-size: 1.25rem;
  line-height: 1.75rem;
}
.article-card__image {
  width: 100%;
  height: 8.4375rem;
  border-radius: 0.25rem 0.25rem 0 0;
}
.article-card__link {
  display: block;
  margin-top: 2rem;
  font-size: 0.8rem;
}
.article-card__description {
  font-size: 0.8rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 6;
  overflow: hidden;
}

.latest-news {
  margin-bottom: 1.25rem;
}
.latest-news-card {
  display: flex;
  flex-direction: column;
  background-color: var(--background2);
  border-radius: 0.25rem;
}
.latest-news-card__container {
  padding-left: 0.75rem;
}
.latest-news-card__heading {
  font-weight: 700;
  font-size: 1.25rem;
  margin-top: 0.5rem;
  letter-spacing: 0.05rem;
}
.latest-news-card__heading a:hover {
  color: var(--pale-violet);
}
.latest-news-card__description {
  margin: 0.375rem;
}
.latest-news-card__image {
  border-radius: 0.25rem 0.25rem 0 0;
}
.latest-news-card__link {
  text-transform: uppercase;
  margin-top: 0.25rem;
}
.latest-news-card__link:hover {
  color: var(--pale-violet);
}
.lists-buttons {
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: var(--background2);
  padding: 0.3rem;
}
.lists-buttons button {
  background-color: transparent;
  border: none;
  margin: 0.3rem;
  cursor: pointer;
}

.film {
  display: flex;
  flex-direction: column;
  min-height: 90vh;
}
.film-background {
  background-size: cover;
  background-repeat: no-repeat;
  height: 250px;
  max-height: 250px;
}
.film-page {
  display: flex;
  flex-direction: column;
  padding: 0 1.25rem;
}
.film-page-heading {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  flex-wrap: wrap;
  gap: 0.5rem;
}
.film-page-heading__title {
  font-weight: 600;
  line-height: 2.25rem;
  font-size: 2.25rem;
}
.film-page-heading__production {
  display: flex;
  flex-direction: row;
  gap: 0.25rem;
  font-size: 1rem;
}
.film-page-heading__production .year {
  font-style: italic;
  text-decoration: underline;
}
.film-page-heading__production .director span {
  text-decoration: underline;
  font-style: italic;
}
.film-page-info__slogan {
  padding-top: 0.75rem;
  text-transform: uppercase;
  font-weight: 500;
}
.film-page-info__cast > p {
  font-size: 1rem;
  font-weight: 700;
  padding-bottom: 0.25rem;
}
.film-page-info__cast .container {
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
}
.film-page-info__cast .container p {
  padding: 0.25rem;
  line-height: 1rem;
  text-transform: capitalize;
  background-color: var(--background2);
  border-radius: 0.25rem;
}
.film-page-info__description {
  padding-top: 0.75rem;
}
.film-cover {
  position: relative;
  width: 7rem;
  height: 10rem;
  margin-top: -20%;
  flex-shrink: 0;
  overflow: hidden;
  border-radius: 0.25rem;
  border: 1.5px solid var(--border-grey);
  box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.0509803922);
}
.film-cover__lists-buttons {
  display: block !important;
}
.film-review-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.5rem;
  margin-bottom: 1.25rem;
}
.film-review-form__input {
  border-radius: 0.25rem;
  background-color: var(--background2);
  padding: 0.75rem;
  border: none;
  width: 100%;
  height: 5rem;
  color: #fff;
}
.film-review-form__input:focus {
  outline: none;
}
.film-review-form__submit {
  border-radius: 0.25rem;
  padding: 0.8rem;
  background-color: var(--background2);
  color: #fff;
  width: 50%;
  font-weight: 700;
  align-self: center;
  border: none;
  cursor: pointer;
  transition: 0.1s ease;
}
.film-review-form__submit:hover {
  background-color: var(--pale-violet);
  color: var(--background2);
  transition: 0.1s ease;
}
.film-review-form__submit:focus {
  outline: none;
}

.watchlist {
  min-height: 90vh;
  padding: 1.25rem 1rem;
}
.watchlist h1 {
  margin-bottom: 1.31rem;
  font-weight: 700;
}

.profile {
  display: flex;
  flex-direction: column;
  min-height: 90vh;
  width: 100%;
}
.profile-background {
  height: 250px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.profile-avatar {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  border: 2px solid var(--border-grey);
  margin-top: -45px;
  width: 90px;
  height: 90px;
  overflow: hidden;
}
.profile-avatar img {
  width: 100px;
  height: 100px;
  min-height: 100%;
}
.profile-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1.25rem;
}
.profile-page-header {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.profile-page-header__name {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.25rem;
  text-align: center;
  margin: 0.5rem 0;
  color: var(--pale-violet);
}
.profile-page-header__bio {
  text-align: center;
}
.profile-page-header__profile-list-counter {
  margin-top: 1rem;
  margin-bottom: 3.5rem;
}
.profile-page__flex {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.profile-page__container {
  align-self: flex-start;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.profile-page__container h2 {
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}
.profile-page__container a {
  font-size: 0.8rem;
  font-weight: 600;
}
.profile-page__container a:hover {
  color: var(--pale-violet);
}
.profile .profile-list-counter {
  display: flex;
  flex-direction: row;
}
.profile .profile-list-counter a {
  width: 50%;
  padding: 0 0.75rem;
}
.profile .profile-list-counter a .counter {
  font-weight: 700;
}
.profile .profile-list-counter a:hover {
  color: var(--pale-violet);
}
.profile .profile-list-counter a p {
  text-align: center;
}
.profile .profile-list-counter__favorites {
  border-right: 2px solid var(--border-grey);
}
.profile-favorites, .profile-reviews, .profile-watched, .profile-watchlist {
  width: 100%;
}
.profile-favorites-section, .profile-watched-section, .profile-watchlist-section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 0.25rem;
}
.profile-favorites-section__film-link {
  width: 16%;
}
.profile hr {
  margin-bottom: 1.25rem;
}
.profile-watchlist-section__film-link {
  width: 16%;
}
.profile .review-card {
  display: flex;
  flex-direction: row;
  padding: 0.5rem 0;
  gap: 0.75rem;
  border-bottom: 2px solid var(--border-grey);
}
.profile .review-card__info a {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 700;
}
.profile .review-card__info a:hover {
  color: var(--pale-violet);
}
.profile .review-card__info span {
  margin-left: 0.3rem;
  font-size: 0.875rem;
  color: var(--grey);
}
.profile .review-card__info p {
  font-size: 0.875rem;
  padding-top: 0.5rem;
  color: var(--grey);
}
.profile .review-card__film-link {
  max-width: 80px;
  max-height: 120px;
}
.profile-watched-section__film-link {
  width: 16%;
}
.profile-reviews {
  margin-top: -0.5rem;
  padding-bottom: 3rem;
}
.profile-reviews-section {
  display: flex;
  flex-direction: column;
}

.burger-menu {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease, pointer-events 0.3s;
  z-index: 12;
}
.burger-menu__background {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9;
  background-color: var(--background);
  opacity: 0.7;
}
.burger-menu__aside {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  top: 0;
  left: 0;
  transform: translateX(-100px);
  width: 70%;
  height: 100%;
  background-color: var(--background);
  box-shadow: 6px 6px 6px 2px var(--backlight);
  padding: 2.5rem 1.25rem 0 1.25rem;
  overflow-y: auto;
  z-index: 10;
  transition: transform 0.3s ease;
}
.burger-menu__profile-info {
  margin-top: 1.5rem;
  margin-bottom: 2rem;
}
.burger-menu__login-button {
  margin: 1rem 0;
}
.burger-menu__logout-button {
  margin: 1rem 0;
}

.burger-menu_active {
  opacity: 1;
  pointer-events: auto;
  transition: opacity 0.3s ease, pointer-events 0.3s;
}
.burger-menu_active .burger-menu__aside {
  transform: translateX(0);
  transition: transform 0.3s ease;
}

.nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  height: auto;
}

.burger-button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}

.profile-info {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}
.profile-info__container {
  margin-left: 1rem;
}
.profile-info__name {
  color: var(--pale-violet);
  font-size: 1rem;
  font-weight: 600;
}
.profile-info__tag {
  font-size: 0.75rem;
  font-weight: 400;
  cursor: pointer;
}
.profile-info__tag::after {
  position: relative;
  bottom: 0.3rem;
  content: "ðŸ—";
  margin-left: 0.2rem;
}
.profile-info__avatar {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background-color: #fff;
}

.nav-link {
  display: flex;
  align-items: center;
  width: 100%;
  height: 2rem;
  border-radius: 0.625rem;
  color: var(--grey);
  font-size: 0.625rem;
  font-weight: 600;
  padding: 0.5rem 0 0.44rem 0.5rem;
}
.nav-link > img {
  margin-right: 0.94rem;
  filter: invert(99%) sepia(88%) saturate(2%) hue-rotate(92deg) brightness(113%) contrast(100%);
  opacity: 0.5;
}

.nav-link_active {
  transition: 0.2s;
  background-color: var(--pale-violet);
  color: var(--background);
}
.nav-link_active > img {
  filter: invert(12%) sepia(26%) saturate(936%) hue-rotate(206deg) brightness(100%) contrast(100%);
  opacity: 1;
}

.error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  padding: 0 1.25rem;
}
.error__image {
  display: inline-block;
  margin-bottom: 0.5rem;
  box-shadow: inset 10px 20px 80px 60px var(--background);
}
.error__image img {
  position: relative;
  z-index: -1;
}
.error__heading {
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  margin-bottom: 1rem;
}
.error > a {
  display: inline-block;
  padding-bottom: 0.3rem;
}
.error > a::after {
  display: block;
  content: "";
  transform: scaleX(0);
  border-bottom: 2px solid var(--pale-violet);
  transition: transform 0.15s ease-in-out;
}
.error > a:hover::after {
  transform: scaleX(1);
}

.login {
  display: flex;
  align-items: center;
  height: 100vh;
}
.login__background {
  position: absolute;
  width: 100%;
  height: 300px;
  overflow: hidden;
  top: 0;
}
.login__background::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 300px;
  width: 100%;
  background-image: url("/public/assets/login/background.jpg");
  transform: rotate(-25deg) scale(1.3);
  transform-origin: bottom left;
  filter: brightness(0.4);
  z-index: -1;
}
.login-page {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0 1.25rem;
  z-index: 1;
}
.login-form {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
.login-form__warning {
  width: 100%;
  background-color: var(--red);
  color: var(--background2);
  margin-bottom: 0.5rem;
  padding: 0.5rem;
  font-weight: 600;
  border-radius: 0.25rem;
  opacity: 0;
  max-height: 0;
}
.login-form__warning_active {
  opacity: 1;
  max-height: 100%;
  transition: 0.2s ease;
}
.login-form__heading {
  font-size: 1.5rem;
  line-height: 2.5rem;
  font-weight: 700;
}
.login-form__greeting {
  margin-bottom: 1rem;
}
.login-form__password, .login-form__username {
  width: 100%;
  padding: 1rem 1.75rem;
  margin-bottom: 1rem;
  border-radius: 0.25rem;
  outline: none;
  border: none;
  background-color: var(--background2);
  font-weight: 600;
  color: #fff;
  margin-bottom: 0.5rem;
  background-image: url("/src/pages/LoginPage/password-icon.svg");
  background-repeat: no-repeat;
  background-position: left 0.5rem center;
}
.login-form__password::placeholder, .login-form__username::placeholder {
  color: #fff;
}
.login-form__password:focus, .login-form__username:focus {
  background-color: var(--grey);
  color: #fff;
}
.login-form__password_invalid, .login-form__username_invalid {
  background-color: var(--pink);
  border: 2px solid var(--red);
  transition: 0.1s ease;
  color: var(--background2);
}
.login-form__username {
  background-image: url("/src/pages/LoginPage/login-icon.svg");
  background-repeat: no-repeat;
  background-position: left 0.5rem center;
}
.login-form__submit {
  color: var(--background);
  background-color: var(--pale-violet);
  padding: 1rem 1.25rem;
  margin-top: 0.5rem;
  width: 50%;
  font-weight: 700;
  font-size: 1rem;
  outline: none;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.1s ease-out, color 0.1s ease-out;
}
.login-form__submit:hover {
  background-color: var(--background2);
  color: #fff;
  transition: 0.2s ease-out;
}
.login-form__restore {
  align-self: flex-end;
  font-size: 0.875rem;
  margin-bottom: 1rem;
}
.login-form__restore:hover {
  color: var(--pale-violet);
}
.login-form__tip {
  margin: 1.5rem 0;
}
.login-form__tip a:hover {
  color: var(--pale-violet);
}

.onboarding {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  padding: 0 1.25rem;
  min-height: 100vh;
}
.onboarding__background {
  position: absolute;
  width: 100%;
  top: 0;
  height: 400px;
  background-image: url("/src/pages/OnBoarding/onboarding-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  box-shadow: inset 0px -80px 80px 60px var(--background);
  z-index: -1;
}

.onboarding__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.onboarding__container .container__text {
  text-align: center;
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 2rem;
}
.onboarding__container .container__link {
  color: var(--background);
  background-color: var(--pale-violet);
  padding: 1rem 1.25rem;
  width: 50%;
  text-align: center;
  font-weight: 700;
  font-size: 1rem;
  outline: none;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.1s ease-out, color 0.1s ease-out;
}
.onboarding__container .container__link:hover {
  background-color: var(--background2);
  color: #fff;
  transition: 0.2s ease-out;
}

.article {
  min-height: 90vh;
}
.article-heading {
  margin: 1.25rem;
  font-weight: 700;
  font-size: 1.5rem;
}
.article-body {
  margin-top: 1.25rem;
}
.article-body__date {
  margin: 0 0 0.5rem 0;
}
.article-body__date__heading {
  font-weight: 700;
  font-size: 1rem;
}
.article-body__date > i {
  font-weight: 600;
}
.article-page {
  margin-left: 1.25rem;
  margin-right: 1.25rem;
}

.search {
  min-height: 90vh;
  padding: 0;
}
.search__input {
  width: 100%;
  height: 3.5rem;
  padding: 1rem;
  color: #fff;
  background-color: var(--background-light);
  border: none;
  outline: none;
}
@media (min-width: 767.99px) {
  body {
    margin-bottom: 0;
    box-shadow: inset -15px 0 15px -10px var(--background2), inset 15px 0 15px -10px var(--background2);
  }
  .bottom-nav {
    display: none;
  }
  .home {
    max-width: 950px;
    margin-left: auto;
    margin-right: auto;
  }
  .home .home-features {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  .home-unauthorized {
    height: 650px;
    max-height: 650px;
    box-shadow: inset 5px -46px 80px 109px var(--background);
    z-index: -1;
  }
  .home__heading {
    margin-top: 50%;
    font-size: 2rem;
    line-height: 2.5rem;
  }
  .home__heading_authorized {
    margin-top: 1.31rem;
  }
  .home .home-feature {
    text-align: center;
  }
  .burger-menu__aside {
    width: 40%;
  }
  .burger-button {
    display: none;
  }
  .header {
    padding: 0;
    transition: box-shadow 0.2s ease-out;
  }
  .header:hover {
    box-shadow: inset -10px 10px 20px 5px var(--backlight);
    transition: 0.2s ease-out;
  }
  .header-container {
    max-width: 950px;
    margin-right: auto;
    margin-left: auto;
    padding: 0 1.25rem;
  }
  .header-container__logo {
    position: static;
    transform: translateX(0);
  }
  .header-nav {
    display: block;
  }
  .recent-articles .container {
    display: grid;
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  .article-card {
    margin-bottom: 0.75rem;
    margin-right: 0.75rem;
  }
  .latest-news-card {
    flex-direction: row;
  }
  .latest-news-card__image {
    width: 100%;
    object-fit: cover;
  }
  .popular-month-lists-grid {
    flex-direction: row;
  }
  .popular-month-section {
    flex-wrap: nowrap;
    gap: 0.5rem;
  }
  .burger-menu {
    display: none;
  }
  .footer .footer-list {
    display: flex;
    flex-direction: row;
  }
  .film {
    max-width: 950px;
    margin-right: auto;
    margin-left: auto;
  }
  .film-page {
    flex-direction: row;
  }
  .film-page-info {
    margin-left: 2.5rem;
  }
  .film-background {
    margin-top: -5%;
    height: 500px;
    max-height: 500px;
    box-shadow: inset 5px -46px 80px 109px var(--background);
  }
  .film-cover {
    align-self: center;
    width: 14rem;
    height: 20rem;
  }
  .film-link_profile {
    max-width: 140px;
    max-height: 220px;
  }
  .film-link:hover .lists-buttons {
    display: block;
  }
  .error {
    max-width: 950px;
    margin-right: auto;
    margin-left: auto;
  }
  .error__image {
    box-shadow: inset 5px 40px 90px 110px var(--background);
  }
  .profile {
    max-width: 950px;
    margin-right: auto;
    margin-left: auto;
  }
  .profile-avatar {
    width: 100px;
    height: 100px;
    margin-top: 0;
    left: 0;
    transform: translateY(-30%);
  }
  .profile-page-header {
    margin-top: 0.5rem;
    flex-direction: row;
  }
  .profile-page-header__profile-list-counter {
    justify-self: flex-end;
    margin: 0;
    margin-left: auto;
    height: fit-content;
  }
  .profile-page-header__container {
    margin-left: 1rem;
  }
  .profile-page-header__name {
    text-align: start;
    margin-top: 0;
  }
  .profile-page__flex {
    gap: 3rem;
    flex-direction: row;
    justify-content: space-between;
  }
  .profile .review-card__film-link {
    width: 140px;
    height: 220px;
  }
  .login-page {
    max-width: 950px;
    margin-left: auto;
    margin-right: auto;
  }
  .login__background {
    box-shadow: inset 0px -80px 80px 60px var(--background);
  }
  .login__background::before {
    transform: rotate(0);
  }
  .onboarding {
    max-width: 950px;
    margin-left: auto;
    margin-right: auto;
  }
  .onboarding__background {
    height: 600px;
  }
  .article {
    max-width: 950px;
    margin: 0 auto;
  }
}